import { Component, OnInit, Input } from '@angular/core';
import { AmChartsService, AmChart } from "@amcharts/amcharts3-angular";
import { HttpClient } from '@angular/common/http';
import { SharedService } from '../../services/shared.service';

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html',
  styleUrls: ['./charts.component.css']
})
export class ChartsComponent {
  private chart: AmChart;
  @Input() dataProvide:any;
  constructor(private AmCharts: AmChartsService, private http: HttpClient, private ser: SharedService) {}

  ngAfterViewInit() {
    console.log(this.dataProvide)
    console.log(this.ser.units[this.dataProvide])
    this.ser.currentunit = this.ser.units[this.dataProvide]
    let data = [
      {
        "category": "waterunit",
        "column-1": parseInt(this.ser.currentunit['waterunit'])
      },
      {
        "category": "totalunit",
        "column-1": parseInt(this.ser.currentunit['totalunit'])
      }
    ]
    this.chart = this.AmCharts.makeChart("chartdiv"+this.dataProvide, {
      "type": "pie",
      "theme": "light",
      "titleField"  : "category",
      "valueField"  : "column-1",
      "dataProvider": data
    });


  }

  ngOnDestroy() {
    if (this.chart) {
      this.AmCharts.destroyChart(this.chart);
    }
  }
}
