import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../services/shared.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-uploadedfiles',
  templateUrl: './uploadedfiles.component.html',
  styleUrls: ['./uploadedfiles.component.css']
})
export class UploadedfilesComponent implements OnInit {

  isChart:boolean = false
  units:any = []
  constructor(private ser: SharedService, private http: HttpClient) { }

  ngOnInit() {
    this.http.post('api/get_all_units', {})
    .subscribe((res:any) => {
      console.log(res);
      this.ser.units = res.units
      this.units = res.units
      this.isChart = true
      alert('SUCCESS !!');
    }, error=>{
      console.log(error)
      alert('Error');
    })
  }
  sendValue(index){
    console.log(this.units[index])
  }

}
